<template>
  <div v-if="stage">
    <div class="playersListsWrap">
      <div>
        <h3 v-if="!registeredUsers.length">Нет зарегистрированных игроков</h3>
        <h3 v-if="registeredUsers.length">Зарегистрированные игроки:</h3>
        <ol>
          <li v-for="user in registeredUsers" class="userListItem">
            <span class="userName">{{user.name}}</span>
            <button @click.stop="unregisterUser(user.id)">x</button>
          </li>
        </ol>
        <span>Осталось <strong>{{placesLeft}}</strong></span>
      </div>

      <div v-if="registeredUsers.length !== seeds">
        <h3>Добавление игроков:</h3>
        <ol>
          <li v-for="user in pickedUsers" class="userListItem">
            <span>{{user.name}}</span>
            <button @click.stop="removeUser(user)">x</button>
          </li>
        </ol>

        <suggest
          v-if="!isFull"
          :items="suggestions"
          @input="findUser"
          placeholder="Начните набирать имя игрока"
          @pick="pickUser"
        />
        <button @click="saveChanges">Зарегистрировать этих игроков на этап</button>
      </div>
    </div>
    <button @click="finishRegistration">Завершить регистрацию!</button>
  </div>
</template>

<script>
  import {mapState} from 'vuex';
  import api from './api';

  import Suggest from './Suggest.vue';

  export default {
    name: 'new-stage',
    components: {
      Suggest
    },
    data: function () {
      return {
        suggestions: [],
        inputValue: '',
        pickedUsers: []
      };
    },
    computed: {
      ...mapState({
        stage: state => state.interface.selectedStage
      }),
      seeds: function() {
        return Number(this.stage.net_size);
      },
      registeredUsers: function() {
        return this.stage.registrations || [];
      },
      placesLeft: function() {
        return this.seeds - this.registeredUsers.length - this.pickedUsers.length;
      },
      isFull: function() {
        return !this.placesLeft;
      }
    },
    methods: {
      updateStage(data) {
        this.$store.commit('editSelectedStage', data);
      },
      updateRegisteredUsers(rawData) {
        this.updateStage({
          registrations: Array.isArray(rawData) ? rawData : Object.values(rawData)
        });
      },
      findUser(e) {
        this.inputValue = e.target.value.trim();

        if (!this.inputValue) {
          return;
        }

        api.suggestUser(e.target.value)
          .then(d => {
            this.suggestions = Object.values(d).map(i => ({...i, display: i.name}));
          });
      },
      pickUser(user) {
        this.suggestions = [];
        if (
          !this.pickedUsers.find(pickedUser => user.id === pickedUser.id) &&
          !this.registeredUsers.find(pickedUser => user.id === pickedUser.id)
        ) {
          this.pickedUsers = this.pickedUsers.concat(user);
        }
      },
      removeUser(user) {
        this.pickedUsers = this.pickedUsers.filter(u => u.id !== user.id);
      },
      unregisterUser(userId) {
        return api.unregisterPlayerFromStage(userId, this.stage.id)
          .then(users => {
            this.updateRegisteredUsers(users);
          });
      },
      saveChanges() {
        api.submitTournamentRegistrationUsers(this.pickedUsers, this.stage.id)
          .then(users => {
              this.updateRegisteredUsers(users);
              this.pickedUsers = [];
          });
      },
      finishRegistration() {
        if (confirm('Вы уверены? Это действие необратимо, больше зарегистрировать игроков будет нельзя!')) {
          api.closeRegistration(this.stage.id)
            .then(() => this.$store.dispatch('selectStage', this.stage.id));
        }
      }
    }
  }
</script>

<style scoped>
  .playersListsWrap {
    display: flex;
    gap: 30px;
  }

  .userListItem {
    display: flex;
    justify-content: space-between;
  }

  .userName {
    margin-right: 20px;
  }
</style>



