<template>
  <div id="app">
    <nav class="nav">
      <router-link to="/">
        Главная
      </router-link>

      <router-link to="/tournaments">
        Турниры
      </router-link>

      <router-link to="/players">
        Игроки
      </router-link>

      <router-link to="/service">
        Обслуживание
      </router-link>
    </nav>
    <router-view></router-view>
  </div>
</template>

<script>
  export default {
    name: 'App',
    mounted: function() {
      this.$store.dispatch('getTournaments');
    },
    computed: {
      isOnMainPage: function() {
        return this.$route.name === 'main';
      }
    }
  }
</script>

<style scoped>
  .nav {
    display: flex;
    gap: 20px;
  }

  .router-link-exact-active {
    color: black;
  }
</style>



